.Book {
  position: relative;
  border-radius: 10px;
  width: clamp(200px, 20vw, 300px); /* scales between 200–300px */
  height: clamp(250px, 35vh, 400px); /* scales vertically */
  background-color: #CEFDFE;
  box-shadow: 1px 1px 12px #000;
  transform: preserve-3d;
  perspective: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #51a6a7;
  transition: transform 0.5s, z-index 0.5s;
  font-size: clamp(1rem, 1.2vw, 1.5rem); /* responsive text size */
  z-index: 0;
  text-align: center;
  padding: 1rem;
}

.Cover,
.Page {
  padding: 1rem;
  top: 0;
  position: absolute;
  background-color: #CEFDFE;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.5s;
  transform-origin: 0;
  box-shadow: 1px 1px 12px #000;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 0.8rem;
  text-align: center;
}

/* Hover animations */
.Book:hover .Cover {
  transform: rotateY(-70deg);
}

.Page {
  transform-origin: 100%;
}

.Book:hover .Page {
  transform: rotateZ(25deg) rotateX(-30deg) rotateY(-10deg) translateX(140px);
  box-shadow: 1px 1px 20px #000a;
}

.Book:hover {
  transform: rotateZ(-10deg);
  z-index: 10;
}

/* Typography */
.Book-Title {
  font-weight: bold;
  font-size: clamp(1rem, 2vw, 1.4rem);
  text-align: center;
  word-wrap: break-word;
  max-width: 90%;
}

.Book-Link {
  margin-top: 1rem;
  text-decoration: none;
  background-color: #CEFDFE;
  color: #51a6a7;
  font-weight: bold;
  border: 1px solid #51a6a7;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.Book-Link:hover {
  background-color: #51a6a7;
  color: white;
}

.Book-Tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 0.5rem;
}


@media (max-width: 900px) {
  .Book {
    width: clamp(180px, 40vw, 280px);
    height: clamp(220px, 40vh, 350px);
    font-size: clamp(1rem, 2vw, 1.3rem);
  }

  .Book-Title {
    font-size: 1.2rem;
  }
}

@media (max-width: 600px) {
  .Book {
    width: 90%;
    height: auto;
    min-height: 250px;
    font-size: clamp(0.9rem, 4vw, 1.2rem);
  }
  .Cover {
    padding: 0.8rem;
    gap: 0.6rem;
  }

  .Page {
    padding: 0.75rem;
  }
}
